@import '../../../styles/variables.module.scss';

.card-list {
  width: 100%;
  .card {
    border: 1px solid $border;
    border-radius: calc(0.25 * #{$unit});
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: calc(2 * #{$unit});
    background-color: $backgroundLight;
    @media (min-width: $md) {
      max-width: 50%;
    }
    .card-row {
      width: 100%;
      display: flex;
      flex-direction: column;
      &:last-child {
        .data-cell {
          &:last-child {
            p {
              width: 100%;
              a {
                width: 100%;
              }
            }
          }
        }
      }
      .data-cell {
        a {
          width: 100%;
        }
        &:first-child {
          background-color: $primaryColorLight;
          p {
            font-weight: 600;
            text-transform: capitalize;
          }
        }
        &:last-child {
          justify-content: start;
        }
      }
    }
    .display-specializari,
    .display-zone,
    .display-servicii,
    .display-terapeuti,
    .display-comenzi,
    .display-admin {
      &:nth-child(1) {
        display: none;
      }
      &:nth-child(2) {
        .data-cell {
          &:last-child {
            p {
              color: $primaryColor;
            }
          }
        }
      }
    }
    .display-servicii {
      &:nth-child(8) {
        display: none;
      }
    }
    .display-terapeuti {
      &:nth-child(7) {
        display: none;
      }
    }
  }
}
.table {
  width: 100%;
  display: flex;
  flex-direction: column;
  .table-header,
  .table-row {
    width: 100%;
    display: flex;
    align-items: center;
    p {
      width: 100%;
    }
    .data-cell {
      &:last-child {
        p {
          justify-content: end;
        }
      }
    }
  }
  .table-header {
    background-color: $primaryColorLight;
    p {
      font-weight: 600;
      text-transform: capitalize;
    }
    .data-cell {
      p {
        text-transform: capitalize !important;
      }
    }
  }
  .display-servicii {
    .data-cell {
      &:nth-child(1),
      &:nth-child(8) {
        display: none;
      }
      &:nth-child(2) {
        max-width: 64px;
      }
      &:nth-child(3) {
        min-width: 300px;
      }
    }
  }
  .display-specializari,
  .display-zone {
    .data-cell {
      &:nth-child(1) {
        display: none;
      }
      &:nth-child(2) {
        max-width: 64px;
      }
    }
  }
  .display-terapeuti {
    .data-cell {
      &:nth-child(1),
      &:nth-child(6),
      &:nth-child(7) {
        display: none;
      }
      &:nth-child(2) {
        max-width: 64px;
      }
      &:nth-child(3),
      &:nth-child(4) {
        max-width: 160px;
      }
      &:nth-child(6) {
        p {
          text-transform: lowercase;
        }
      }
      &:nth-child(8) {
        p {
          text-transform: lowercase;
        }
      }
      &:last-child {
        max-width: 112px;
      }
    }
  }
  .display-comenzi {
    .data-cell {
      &:nth-child(1),
      &:nth-child(7),
      &:nth-child(8),
      &:nth-child(10),
      &:nth-child(11),
      &:nth-child(13) {
        display: none;
      }
    }
    .data-cell {
      &:nth-child(2) {
        max-width: 64px;
      }
    }
    .data-cell {
      &:nth-child(3) {
        max-width: 96px;
      }
    }
    .data-cell {
      &:nth-child(11) {
        min-width: 192px;
      }
    }
    .data-cell {
      &:nth-child(12) {
        min-width: 174px;
      }
    }
    .data-cell {
      &:nth-child(14),
      &:nth-child(15) {
        max-width: 80px;
      }
    }
    .data-cell {
      &:last-child {
        max-width: 112px;
      }
    }
  }
  .display-admin {
    .data-cell {
      &:nth-child(1) {
        display: none;
      }
    }
    .data-cell {
      &:nth-child(2) {
        max-width: 64px;
      }
    }
    .data-cell {
      &:nth-child(6) {
        min-width: 192px;
        p {
          text-transform: lowercase;
        }
      }
    }
  }
}
